<template>
  <div class="card">
    <div class="card-body">
      <h3 class="text-dark">
        <strong>Ubah Data Asal Pendidikan</strong>
      </h3>
      <br />
      <form @submit.prevent="submit">
        <div class="form-group">
          <label for="jenjangsklh">Jenjang Sekolah Asal</label>
          <select
            @change="getDataPendidikan"
            class="form-control"
            id="jenjangsklh"
          >
            <option disable v-if="getDataAsal.education_level == null">
              Pilih Jenjang Sekolah Asal
            </option>
            <option disable v-else>
              {{ getDataAsal.education_level }}
            </option>
            <option
              v-for="pendidikan in getDataPendidikan"
              :key="pendidikan.education_level_id"
              :value="pendidikan.education_level_id"
            >
              {{ pendidikan.education_level }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="nmSekolah">Nama Sekolah</label>
          <input
            type="text"
            class="form-control"
            id="nmSekolah"
            placeholder="Nama Sekolah"
            :value="getDataAsal.institution_name"
            :v-model="form.institution_name"
          />
        </div>
        <div class="form-group">
          <label for="stsSekolah">Status Sekolah</label>
          <select class="form-control" id="stsSekolah">
            <option>Negeri</option>
            <option>Swasta</option>
          </select>
        </div>
        <div class="form-group">
          <label for="inputAlamat">Alamat</label>
          <input
            type="text"
            class="form-control"
            id="inputAlamat"
            placeholder="Alamat"
            :value="getDataAsal.address"
          />
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputProv">Provinsi</label>
            <select
              @change="getDataProvinsi"
              id="inputProv"
              class="form-control"
            >
              <option disable v-if="getDataProvinsi.province == null">
                Pilih Provinsi
              </option>
              <option disable v-else>{{ getDataProvinsi.province }}</option>
              <option
                v-for="provinsi in getDataProvinsi"
                :key="provinsi.province_id"
                :value="provinsi.province_id"
              >
                {{ provinsi.province }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputKokab">Kota/Kabupaten</label>
            <select id="inputKokab" class="form-control">
              <option selected>Pilih Kota/Kabupaten...</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputTlp">Telepon</label>
            <input
              type="text"
              class="form-control"
              id="inputTlp"
              placeholder="Telepon"
              :value="getDataAsal.handphone_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="inputPostal">Kode Pos</label>
            <input
              type="text"
              class="form-control"
              id="inputPostal"
              placeholder="Kode Pos"
              :value="getDataAsal.postal"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="inputJurusan">Jurusan</label>
          <input
            type="text"
            class="form-control"
            id="inputJurusan"
            placeholder="Jurusan"
            :value="getDataAsal.major"
          />
        </div>
        <div class="form-group">
          <label for="Akreditasi">Akreditasi</label>
          <select class="form-control" id="Akreditasi">
            <option disable v-if="getDataAsal.accreditation == null">
              Pilih Akreditasi
            </option>
            <option disable v-else>{{ getDataAsal.accreditation }}</option>
            <option>A</option>
            <option>B</option>
            <option>C</option>
          </select>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputThnMsk">Tahun Masuk</label>
            <input
              type="text"
              class="form-control"
              id="inputThnMsk"
              placeholder="Jurusan"
              :value="getDataAsal.yaer_enrty"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="inputThnLls">Tahun Lulus</label>
            <input
              type="text"
              class="form-control"
              id="inputThnLls"
              placeholder="Jurusan"
              :value="getDataAsal.graduation_year"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="NoIjazah">No Ijazah</label>
          <input
            type="text"
            class="form-control"
            id="NoIjazah"
            placeholder="No Ijazah"
            :value="getDataAsal.graduation_letter"
          />
        </div>
        <div class="form-group">
          <label for="inputIPK">Nilai UNAS/IPK</label>
          <input
            type="text"
            class="form-control"
            id="inputIPK"
            placeholder="Nilai UNAS/IPK"
            :value="getDataAsal.graduation_score"
          />
        </div>
        <div class="form-group">
          <label for="stsTransfer">Status Transfer</label>
          <select class="form-control" id="stsTransfer">
            <option>Bukan Transfer</option>
            <option>Transfer</option>
          </select>
        </div>
        <button type="submit" class="btn btn-primary btn-success">
          SIMPAN
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DataPendidikan",
  data() {
    return {
      pendidikan: "",
      form: {
        npm: null,
        address: null,
        province_id: null,
        city_id: null,
        districts_id: null,
        village_id: null,
        rt: null,
        rw: null,
        postal: null,
        education_level_id: null,
        institution_name: null,
        institution_status: null,
        major: null,
        accreditation: null,
        yaer_enrty: null,
        graduation_year: null,
        graduation_letter: null,
        graduation_score: null,
        graduate_category: null,
        handphone_number: null,
      },
    };
  },
  computed: {
    ...mapGetters({
      getDataAsal: "asalpendidikan/datapendidikan",
      getDataPendidikan: "pendidikan/datapendidikan",
      getDataProvinsi: "provinsi/dataprovinsi",
    }),
  },
  methods: {
    ...mapActions({
      datapendidik: "pendidikan/pendidikan",
      datapendidikan: "asalpendidikan/pendidikan",
      dtprovinsi: "provinsi/provinsi",
    }),
    getDTPendidik() {
      this.datapendidik();
    },
    getDTPendidikan() {
      this.datapendidikan();
    },
    getPilihProvinsi() {
      this.dtprovinsi();
    },
    async submit() {
      try {
        let dataEducation = {
          npm: this.form.npm,
          address: this.form.address,
          province_id: this.form.province_id,
          city_id: this.form.city_id,
          districts_id: this.form.districts_id,
          village_id: this.form.village_id,
          rt: this.form.rt,
          rw: this.form.rw,
          postal: this.form.postal,
          education_level_id: this.form.education_level_id,
          institution_name: this.form.institution_name,
          institution_status: this.form.institution_status,
          major: this.form.major,
          accreditation: this.form.accreditation,
          yaer_enrty: this.form.yaer_enrty,
          graduation_year: this.form.graduation_year,
          graduation_letter: this.form.graduation_letter,
          graduation_score: this.form.graduation_score,
          graduate_category: this.form.graduate_category,
          handphone_number: this.form.handphone_number,
        };
        let response = await axios.put("/oasis/education", dataEducation);

        // this.setRiwayatToView();
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getDTPendidikan();
    this.getDTPendidik();
    this.getPilihProvinsi();
  },
};
</script>

<style>
</style>